<template>
	<div
		class="map-container"
		v-title
		data-title="气象监测"
		v-loading="loading"
		element-loading-text="加载中..."
		element-loading-spinner="el-icon-loading"
		element-loading-background="rgba(0, 0, 0, 0)"
	>
		<!--
    http://222.128.59.164:6896/Kingts_Power/#/app/temp
    http://183.230.93.188:8088/Kingts_Power/#/app/wind
    http://platform.kingweather.cn/Kingts_Powers/#/app/temp
    https://www.ventusky.com/
    -->
		<iframe class="scene-map" src="https://www.ventusky.com/" frameborder="0" ref="iframe"></iframe>
		<img class="za-logo" width="125" src="@/assets/images/scene/logo.png" alt="左岸芯慧logo" v-if="false" />
	</div>
</template>

<script>
export default {
	name: 'sceneFrame',
	data() {
		return {
			loading: false
		}
	},
	created() {
		this.loading = true;
	},
	mounted() {
		this.$nextTick(() => {
			this.$refs.iframe.addEventListener('load', () => {
				this.loading = false;
			}, true);
		})
	},
};
</script>

<style scoped lang="scss">
.map-container {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 90;
	.scene-map {
		width: 100%;
		height: 100%;
	}
	.za-logo {
		position: fixed;
		bottom: -13px;
		right: 0.5rem;
		z-index: 999;
		opacity: 0.63;
	}

	&.main-container {
		width: 100% !important;
    height: 100% !important;
	}
}
</style>

<style lang="scss">
.map-container > .el-loading-mask {
	i {
		color: #00e5ff;
		font-size: 40px;
	}
	p {
		color: #00e5ff;
		line-height: 40px;
		font-size: 18px;
	}
}
</style>



